/** 
	Immagine con effetto HOVER
*/

.container-immagine {
	margin: 2em auto;
	position: relative;
  	width: 100%;
}

.immagine {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: .5s ease;
  backface-visibility: hidden;
}

.middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.container-immagine:hover .immagine {
  opacity: 0.3;
}

.container-immagine:hover .middle {
  opacity: 1;
}

.titolo-immagine {
  background-color: $arancione-color;
  color: white;
  font-size: 18px;
  padding: .5em;
}

.testo-immagine {
  background-color: $arancione-color;
  color: white;
  font-size: 14px;
  padding: 1em;
}

.navbar-nav{
